import React from 'react'
import './PlantDetail.scss'
import CareScale from '../care-scale/CareScale'
import MiniCart from '../mini-cart/MiniCart'
/*import basket from '../../medias/basket.svg';
import miniQuit from '../../medias/miniQuit.svg';
import minus from '../../medias/minus.svg';
import plus from '../../medias/plus.svg';*/

function PlantDetail({
    image,
    name,
    price,
    offer,
    light,
    water,
    isOpen,
    closePanel,
    onClick,
    setMiniCartOpen,
    miniCartOpen,
    deviceWidth
}) {


    return isOpen
        ? (
            <div className="Panel">
                <div className="PanelContainer">
                    <img className="image" src={image} alt={name} onClick={closePanel}/>
                    <div className="InformationContainer">
                        <div className="TextContainer">
                            {offer && <div className="tag">On sale</div>}
                            <h1>{name}</h1>
                            <h3>Care instuctions:</h3>
                            <CareScale scaleValue={light} careType="light"/>
                            <CareScale scaleValue={water} careType="water"/>
                            <h4>{`${price.toFixed(2)} €`}</h4>
                        </div>
                        <button className="Button" onClick={onClick}>Add to cart</button>
                    </div>
                </div>

            <MiniCart
                setMiniCartOpen={setMiniCartOpen}
                miniCartOpen={miniCartOpen}
                name={name}
                image={image}
                deviceWidth={deviceWidth}
                message={`You have successfully added to your basket`}/>
            </div>
        )
        : null
}

export default PlantDetail
