import React, {useState, useEffect} from 'react'
import './MiniCart.scss'
import quit from '../../medias/quit.svg';

function MiniCart({
    onClick,
    message,
    name,
    image,
    miniCartOpen,
    setMiniCartOpen,
    deviceWidth
}) {

    const [position,
        setPosition] = useState('-100%')
    const [opacity,
        setOpacity] = useState('0')



    useEffect(() => {
        miniCartOpen && setTimeout(() => {
            setOpacity('0.5')
            setPosition('0%')
        }, 0);

    }, [miniCartOpen])

    function closePanel() {
        setOpacity('0')
        setPosition('-100%')
        setTimeout(() => {
            setMiniCartOpen(false)
        }, 300);
    }

    console.log(window.innerWidth)

    return (miniCartOpen
        ? <div
                style={{
                background: `rgba(0,0,0,${opacity})`
            }}
                className="mini-cart">
                <div
                    className="mini-cart-container"
                    style={deviceWidth < 768 ? {
                        bottom: position
                    }:{
                        right: position
                    }}>
                    <div className="title">
                        <h2>Added to cart</h2>
                        <img
                            className="icon"
                            onClick={closePanel}
                            src={quit}
                            alt="quit"/>
                    </div>

                    <img src={image} alt={image}/>
                </div>
            </div>
        : null)
}

export default MiniCart